/* eslint-disable multiline-ternary */
/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { useQuery, useMutation, useSubscription } from '@apollo/client';
import { createRef, useEffect, useState } from 'react';
import { Badge, Button, Card, Col, Container, Form, Row } from 'react-bootstrap';

import {
  SENDER,
  SerialMessage,
  SerialMessageProps
} from '../../components/SerialMessage/SerialMessage';
import { useHost, usePort } from '../../data/redux/general/generalSelectors';
import { Mutations, Querys, Subscriptions } from '../../data/api';
import { Response } from '../../data/api/types/api.types';

export const HostScreen = (): JSX.Element => {
  const host = useHost() ?? '';
  const port = usePort() ?? '';
  const [messages, setMessages] = useState<SerialMessageProps[]>([]);
  const [command, setCommand] = useState('');
  const [device, setDevice] = useState('none');
  const [baudRate, setBaudRate] = useState('9600');
  const [parity, setParity] = useState('none');
  const [dataBits, setDataBits] = useState('8');

  const [lineEnding, setLineEnding] = useState('\\r');

  const [hupcl, setHUPCL] = useState(false);
  const [lock, setLOCK] = useState(false);
  const [rtscts, setRTSCTS] = useState(false);
  const [xany, setXANY] = useState(false);
  const [xoff, setXOFF] = useState(false);
  const [xon, setXON] = useState(false);

  const [connected, setConnected] = useState(false);

  const {
    loading: deviceListLoading,
    error: deviceListError,
    data: deviceList
  } = useQuery<Response>(Querys.QUERY_GET_DEVICES_LIST);

  const [startSerialDeviceConnectionFunc, { data: startSerialData }] = useMutation<Response>(
    Mutations.MUTATION_START_SERIAL_DEVICE_CONNECTION
  );

  const [sendMessageFunc] = useMutation<Response>(Mutations.MUTATION_SEND_MESSAGE);

  const { startSerialDeviceConnection } = startSerialData ?? {};
  const { successful = false } = startSerialDeviceConnection ?? {};

  const isConnected = connected && successful;

  const messagesEndRef = createRef<HTMLDivElement>();

  useEffect(() => {
    messagesEndRef.current?.scrollTo(0, messagesEndRef.current.scrollHeight);
  }, [messages]);

  const {
    data: messageData,
    loading: messload,
    error: messerr
  } = useSubscription<Response>(Subscriptions.SUBSCRIPTION_DEVICE_MESSAGES, {
    variables: { path: device },
    fetchPolicy: 'no-cache'
  });
  console.log('re', messload, messerr);

  useEffect(() => {
    console.log(messageData);

    if (messageData !== undefined && messageData.deviceMessage !== undefined) {
      setMessages((old) => [
        ...old,
        {
          date: messageData?.deviceMessage?.date ?? '',
          message: messageData?.deviceMessage?.message ?? '',
          sendedBy: SENDER.SERIAL_DEVICE
        }
      ]);
    }
  }, [messageData]);

  return (
    <Container>
      <br />
      <Row>
        <Col xs={5}>
          <Card bg={'primary'} text={'white'} className="text-center" style={{ width: '100%' }}>
            <Card.Header as="h5">General Information</Card.Header>
            <Card.Body>
              <Card.Text>
                <h6>Current Serial Host</h6>
                <p>
                  {host}:{port}
                </p>
                <h6>Current Serial Device</h6>
                {isConnected ? (
                  <Badge bg="success">Connected</Badge>
                ) : (
                  <Badge bg="danger">Not Connected</Badge>
                )}
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
        <Col xs={2}></Col>
        <Col xs={5}>
          <Card bg={'primary'} text={'white'} className="text-center" style={{ width: '100%' }}>
            <Card.Header as="h5">Serial Device</Card.Header>
            <Card.Body>
              <Card.Text>
                <Form
                  onSubmit={(e) => {
                    e.preventDefault();

                    setConnected(true);

                    startSerialDeviceConnectionFunc({
                      variables: {
                        path: device,
                        baudRate: parseInt(baudRate),
                        lock,
                        dataBits: parseInt(dataBits),
                        parity,
                        rtscts,
                        xon,
                        xoff,
                        xany,
                        hupcl
                      }
                    })
                      .then()
                      .catch((err) => console.error(err));
                  }}
                >
                  <Row>
                    <Col>
                      <Form.Label>Device</Form.Label>
                      <Form.Select onChange={(e) => setDevice(e.currentTarget.value)}>
                        {deviceListLoading && deviceListError === undefined ? (
                          <option value="loading">- Loading Device List -</option>
                        ) : (
                          <option value="none">- Choose a Device -</option>
                        )}
                        {deviceListError !== undefined ? (
                          <option value="error">- Error occurred while loading data -</option>
                        ) : null}
                        {deviceList?.getDevicesList?.ports.map((dev, index) => (
                          <option key={dev.path} value={dev.path}>
                            {index} - {dev.path} - {dev.manufacturer ?? 'no manufacturer'}
                          </option>
                        ))}
                      </Form.Select>
                    </Col>
                    <Col xs="3">
                      <Form.Label>Baud Rate</Form.Label>
                      <Form.Control
                        type="number"
                        defaultValue={9600}
                        onChange={(e) => setBaudRate(e.currentTarget.value)}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Form.Label>Parity</Form.Label>
                      <Form.Select onChange={(e) => setParity(e.currentTarget.value)}>
                        <option value="none">none</option>
                        <option value="even">even</option>
                        <option value="odd">odd</option>
                        <option value="mark">mark</option>
                        <option value="space">space</option>
                      </Form.Select>
                    </Col>
                    <Col>
                      <Form.Label>Data Bits</Form.Label>
                      <Form.Select onChange={(e) => setDataBits(e.currentTarget.value)}>
                        <option value="8">8</option>
                        <option value="7">7</option>
                        <option value="6">6</option>
                        <option value="5">5</option>
                      </Form.Select>
                    </Col>
                  </Row>
                  <Row>
                    <br />
                    <h6>Options</h6>
                  </Row>
                  <Row>
                    <Col>
                      <Form.Check
                        type="checkbox"
                        label="hupcl"
                        onChange={(e) => setHUPCL(e.currentTarget.checked)}
                      />
                      <Form.Check
                        type="checkbox"
                        label="lock"
                        onChange={(e) => setLOCK(e.currentTarget.checked)}
                      />
                      <Form.Check
                        type="checkbox"
                        label="rtscts"
                        onChange={(e) => setRTSCTS(e.currentTarget.checked)}
                      />
                    </Col>
                    <Col>
                      <Form.Check
                        type="checkbox"
                        label="xany"
                        onChange={(e) => setXANY(e.currentTarget.checked)}
                      />
                      <Form.Check
                        type="checkbox"
                        label="xoff"
                        onChange={(e) => setXOFF(e.currentTarget.checked)}
                      />
                      <Form.Check
                        type="checkbox"
                        label="xon"
                        onChange={(e) => setXON(e.currentTarget.checked)}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Form.Control
                        type="text"
                        value={lineEnding}
                        onChange={(e) => setLineEnding(e.currentTarget.value)}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <br />
                  </Row>
                  <Row>
                    <Col></Col>
                    <Col xs="auto">
                      <Button
                        variant="success"
                        type="submit"
                        disabled={connected || device === 'none'}
                      >
                        {connected ? 'Already Connected' : 'Connect'}
                      </Button>
                    </Col>
                    <Col></Col>
                  </Row>
                </Form>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col xs={12}>
          <br />
          <Card bg={'dark'} text={'white'} style={{ width: '100%' }}>
            <Card.Header as="h5" className="text-center">
              Serial Console
            </Card.Header>
            <Card.Body
              style={{ height: '20vh', overflowY: 'auto', overflowX: 'hidden' }}
              ref={messagesEndRef}
            >
              {messages.map((msg) => (
                <SerialMessage
                  key={`msg_${msg.date}`}
                  date={msg.date}
                  message={msg.message}
                  sendedBy={msg.sendedBy}
                />
              ))}
            </Card.Body>
            <Card.Footer>
              <Form
                onSubmit={(e) => {
                  e.preventDefault();
                  if (command === '') {
                    return;
                  }
                  setMessages((old) => [
                    ...old,
                    { date: new Date().toISOString(), message: command, sendedBy: SENDER.CONSOLE }
                  ]);
                  sendMessageFunc({
                    variables: {
                      path: device,
                      message: command + lineEnding.replace('\\r', '\r')
                    }
                  })
                    .then()
                    .catch((err) => console.error(err));
                  setCommand('');
                }}
              >
                <Row>
                  <Col>
                    <Form.Control
                      type="text"
                      id="inputSerialCommand"
                      value={command}
                      onChange={(e) => setCommand(e.currentTarget.value)}
                      placeholder="enter command"
                    />
                  </Col>
                  <Col xs="auto">
                    <Button variant="primary" type="submit" disabled={command === ''}>
                      send
                    </Button>
                  </Col>
                </Row>
              </Form>
            </Card.Footer>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};
