import { gql } from '@apollo/client';

export const MUTATION_START_SERIAL_DEVICE_CONNECTION = gql(`
mutation StartSerialDeviceConnection($path: String!, $baudRate: Int!, $dataBits: Int, $hupcl: Boolean, $lock: Boolean, $parity: String, $rtscts: Boolean, $xany: Boolean, $xoff: Boolean, $xon: Boolean) {
    startSerialDeviceConnection(
      options: {path: $path, baudRate: $baudRate, dataBits: $dataBits, hupcl: $hupcl, lock: $lock, parity: $parity, rtscts: $rtscts, xany: $xany, xoff: $xoff, xon: $xon}
    ) {
      options {
        baudRate
        dataBits
        hupcl
        lock
        parity
        path
        rtscts
        xany
        xoff
        xon
      }
      successful
    }
  }`);
