/* eslint-disable multiline-ternary */
import { Routes, Route } from 'react-router-dom';
import { ApolloProvider } from '@apollo/client';
import { ApolloClient, InMemoryCache } from '@apollo/client/core';
import { useMemo } from 'react';
import { useHost, usePort } from '../data/redux/general/generalSelectors';
import { HomeScreen } from '../pages/Home/Home';
import { HostScreen } from '../pages/Host/Host';
import { NotFoundScreen } from '../pages/NotFound/NotFound';
// eslint-disable-next-line @typescript-eslint/no-var-requires
const { YogaLink } = require('@graphql-yoga/apollo-link');

export const Navigation = (): JSX.Element => {
  const host = useHost();
  const port = usePort();

  const client = useMemo(
    () =>
      new ApolloClient({
        link: new YogaLink({
          endpoint: `http://${host ?? ''}:${port ?? ''}/graphql`
        }),
        cache: new InMemoryCache()
      }),
    [host, port]
  );

  return (
    <Routes>
      <Route path="/" element={<HomeScreen />} />
      {host !== undefined ? (
        <Route
          path="/host"
          element={
            <ApolloProvider client={client}>
              <HostScreen />
            </ApolloProvider>
          }
        />
      ) : null}
      <Route path="*" element={<NotFoundScreen />} />
    </Routes>
  );
};
