import React, { useCallback, useState } from 'react';
import './Home.css';
import { Container, Row, Col, Card, Form, Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useAppDispatch } from '../../data/redux/reduxHooks';
import { setHost } from '../../data/redux/general/generalSlice';

export const HomeScreen = (): JSX.Element => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const [smartSerialHost, setSmartSerialHost] = useState('');
  const onConnectPress = useCallback(() => {
    dispatch(setHost(smartSerialHost));
    navigate('/host');
  }, [smartSerialHost]);

  return (
    <Container className="vertical-center">
      <Row>
        <Col xs={3}></Col>
        <Col xs={6}>
          <Card className="text-center">
            <Card.Header as="h5">SmartSerial Connect</Card.Header>
            <Card.Body>
              <Form
                onSubmit={(e) => {
                  e.preventDefault();
                  onConnectPress();
                }}
              >
                <Row>
                  <Form.Label htmlFor="inputHost1">SmartSerial Host</Form.Label>
                </Row>
                <Row>
                  <Form.Control
                    type="text"
                    className="text-center"
                    id="inputHost1"
                    aria-describedby="hostInputHelpBlock"
                    onChange={(t) => setSmartSerialHost(t.currentTarget.value)}
                  />
                </Row>
                <Row>
                  <Form.Text id="hostInputHelpBlock" muted>
                    The hostname of the SmartSerial Host. Examples: 127.0.0.1 or localhost
                  </Form.Text>
                </Row>
                <br />
                <Row>
                  <Col>
                    <Button
                      variant="primary"
                      size="sm"
                      type="submit"
                      disabled={smartSerialHost === ''}
                    >
                      Connect
                    </Button>
                  </Col>
                </Row>
              </Form>
            </Card.Body>
            <Card.Footer className="text-muted">Developed by Ren L.</Card.Footer>
          </Card>
        </Col>
        <Col xs={3}></Col>
      </Row>
    </Container>
  );
};
