import { RootState } from '../redux';
import { useAppSelector } from '../reduxHooks';
import { GeneralState } from './generalSlice';

export const selectGeneral = (state: RootState): GeneralState => state.general;

export const selectHost = (state: RootState): string | undefined => selectGeneral(state).host;

export const useHost = (): string | undefined => useAppSelector((state) => selectHost(state));

export const selectPort = (state: RootState): string => selectGeneral(state).port;

export const usePort = (): string => useAppSelector((state) => selectPort(state));
