import './SerialMessage.css';

export enum SENDER {
  SERIAL_DEVICE = 'SERIAL_DEVICE',
  CONSOLE = 'CONSOLE'
}

export interface SerialMessageProps {
  date: string;
  message: string;
  sendedBy: SENDER;
}

export const SerialMessage = (props: SerialMessageProps): JSX.Element => {
  const { date, message, sendedBy } = props;
  const msgDate = new Date(date);
  return (
    <div
      className="serialMessageContainer"
      style={{ color: sendedBy === SENDER.CONSOLE ? 'red' : 'green' }}
    >
      <span className="serialMessageTime">{msgDate.toLocaleTimeString()}</span>
      <span className="serialMessageMessage">{message}</span>
      <span className="serialMessageSended">{sendedBy}</span>
    </div>
  );
};
